import { Component, Prop, h } from '@stencil/core';


export type TextFieldTheme = 'Siemens-LightTheme' | 'VIQ-DarkTheme';
 

@Component({
  tag: 'my-text-field',
  styleUrl: 'my-text-field.css',
  shadow: true,
})


//add option for emiting the value from this Component to other Component
export class MyTextField
{
    @Prop() textInPlaceholder: string = "Enter your text here";
    @Prop() lablelText: string = "Text Field";
    @Prop() ColorOfLable: string = "black";

    @Prop() Theme: TextFieldTheme = "Siemens-LightTheme";

    @Prop() variantSize: 'small' | 'medium' | 'large' = 'medium';

    @Prop() IsRequeredToFill : boolean = false;

    @Prop() size: number = 20;

    @Prop() disable: boolean = false;


    render(){

        const classesInput={
            'Siemens-LightTheme': this.Theme === 'Siemens-LightTheme',
            'VIQ-DarkTheme': this.Theme === 'VIQ-DarkTheme',

            'small': this.variantSize === 'small',
            'medium': this.variantSize === 'medium',
            'large': this.variantSize === 'large',
        };
        return(<div>
            <form>
                <label htmlFor="TextField" id="labelText" style={{ color: this.ColorOfLable }}>{this.lablelText}</label>
                <input type="text" placeholder={this.textInPlaceholder} id="TextField" class={classesInput} required={this.IsRequeredToFill} size={this.size} disabled={this.disable}></input>
            </form>
        </div>)
    }
}
